@media (max-width : 980px){
  .dish{
      width: 200px;
  }
  #my-menu{
      display: none;
  }
  #menu-btn{
      display: block !important;
  }
  li{
      padding: 16px;
      font-size: 20px;
  }
}


@media (max-width : 768px){
  #dishes>span{
      font-size: 32px;
  }
  #flex-items{
    flex-direction: column;
    align-content: center;
  }
  #container-text{
    padding: 60px 20px;
  }
  #big-text{
      font-size: 32px;
  }
}

@media (max-width : 470px){
  #logo{
      font-size: 1.75rem;
  }
  #container-text{
      padding: 20px;
      max-width: 220px;
  }
  #container-text>h1{
      font-size: 16px;
  }
  .mobileDevices{
      top: 63px;
  }
}

#element{
  width: 100%;
  max-width: 200px;
}

body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Comfortaa", serif;
}
a:hover{
  cursor: pointer;
}
#navbar{
    background-color:#403E3E;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: 3px solid #302f2f;
}

#navbar_content{
  padding: 16px 25px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

#logo{
  color: #fff;
  font-size: 36px;
  display: inline-block;
  margin-right: 35%;
}

ul{
  list-style: none;
}

li{
    display: inline-block;
    box-sizing: content-box;
}

.navlink{
  color:#FFF;
  text-decoration: none;
  font-size: 16px;
  margin: 10px;
  padding: 20px;
}
#menu-btn{
  display: none;
  text-decoration: none;
  color: #fff;
  margin-left: auto;
}
.navlink:hover{
  background: #393939;
}
.mobileDevices{
  display: flex!important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 73px;
  left: 0;
  right: 0;
  bottom: 0;
  border-top: 1px solid grey;
  background: #403E3E;
  margin: 0;
}
header{
    background-image: url(/static/media/food.dcd9e3e7.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 300px 0;
}

#container-text{
    margin-left: auto;
    margin-right: auto;
    background-color: #403E3E;
    border: 3px solid #929090;
    color: white;
    padding: 77px 129px;
    border-radius: 30px;
    width: 300px;
    text-align: center;
    z-index: 30;
}
#dishes{
    background-color: #C4C4C4;
    text-align: center;
    color: white;
    padding: 100px 64px;
  
  }
  
  #dishes>span{
    text-transform: uppercase;
    font-size: 48px;
    margin-bottom: 32px;
    display: inline-block;
  }
  
  #flex-items{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  .dish{
    background-color: #403E3E ;
    border-radius: 16px;
    padding: 50px 40px;
    width: 280px;
    margin: 10px;
  }
  
  .dish>span{
    display: block;
    font-size: 28px;
    margin-bottom: 7px;
  }
  
  .dish-btn{
    margin-top: 16px;
    color: white;
    font-size: 17px;
    text-decoration: none;
    padding: 8px 16px;
    border: 2px solid lightgrey;
    border-radius: 8px;
    display: inline-block;
  }
  
  .dish-btn:hover{
    color: rgba(255,255,255,0.6);
    text-decoration: none;
    border: 2px solid rgba(255,255,255,0.6);
  }
#description{
    background-image: url(/static/media/food2.7a534e76.jpg);
    padding: 300px 0;
    display: flex;
    justify-content: center;
  }
  
  #some-text{
      background-color: #403E3E;
      color:white;
      display: inline-block;
      text-align: center;
      padding: 16px;
      width: 80%;
  }
  
  #big-text{
    font-size: 36px;
    margin: 16px;
    display: block;
  }

  #small-text{
    font-size: 30px;
    display: block;
    }
footer
{
  background-color: #403E3E;
  color: white;
  display: flex;
  flex-flow: column wrap;
  align-items:  center;
  text-align: center;
  padding: 64px 8px;
}

footer>span{
  margin: 16px;
  font-size: 28px;
}

.network{
  display: flex;
  align-items: center;
  width: 128px;

}

.network>a{
  text-decoration: none;
  color: white;
}

.network>a:hover{
  cursor: pointer;
  color: black;
}

.svg{
  width: 32px;
  height: 32px;
  margin: 5px;
}
section{
  background-color: #C4C4C4;
}

#my-form{
    display: flex;
    flex-direction: column;
    max-width: 555px;
    width: 100%;
    margin: 0 auto;
    text-align: left;
    font-size: 24px;
  }
  #my-form > label{
    margin-top: 20px;
  }
  
  #discounts{
    font-size: 18px;
    margin-top: 8px;
  }
  input, textarea{
    padding: 8px 12px;
  }
  textarea{
    height: 100px;
    resize: none;
  }
    #feedback-btn{
    display: inline-block;
    padding: 8px 16px;
    color: black;
    margin: 32px 0 0px 0;
    text-align: center;
    font-size: 48px;
    font-weight: bold;
  }
  
  button{
    display: inline-block;
    padding: 8px 16px;
    background: #403E3E;
    color: white;
    margin: 32px 0;
    text-align: center;
    font-family: Comfortaa;
  }

  #comments{
    display: flex;
    flex-direction: column;
    font-size: 36px;
    text-align: center;
  }

  
.comment-item{
    background-color: #403E3E;
    border-radius: 3px;
    margin: 10px auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    max-width: 400px;
    width: 100%;
}

.comment-name{
    color:white;
    font-size: 24px;
    text-align: left;
}

.comment-text{
    color:white;
    font-size: 20px;
    text-align: left;
    margin-top: 15px;
    word-wrap: break-word;
}

.like-container{
    display: flex;
    flex-direction: row;
    padding-right: 10px;
}

i.likes{
    color:red;
}

i{
    color: white;
    font-size: 20px;
    /* float: left; */
    /* text-align: right; */
    margin-left: auto;
}


